const HowToStepper = () => {
  const [activeStep, setActiveStep] = useState(0);
  const classes = useStyles();

  const handleNext = () => {
    setActiveStep((prev) => prev + 1);
  };

  const handlePrev = () => {
    setActiveStep((prev) => prev - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };
  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Stepper activeStep={activeStep} orientation="vertical">
          {steps.map((step, index) => (
            <Step key={step}>
              <StepLabel>
                <Typography variant="h2">{step}</Typography>
              </StepLabel>
              <StepContent>
                <Typography variant="body1">{getStepContent(index)}</Typography>
                <Grid item xs={6} className={classes.topSpace}>
                  <Button
                    size="small"
                    disabled={activeStep === 0}
                    onClick={handlePrev}
                  >
                    Tillbaka
                  </Button>
                  <Button
                    size="small"
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                    className={classes.leftSpace}
                    disabled={activeStep === steps.length - 1}
                  >
                    NÃ¤sta
                  </Button>
                </Grid>
              </StepContent>
            </Step>
          ))}
        </Stepper>
        {activeStep === steps.length && (
          <Grid item xs={12} className={classes.leftSpace}>
            <Typography variant="body1">SÃ¥ lÃ¤tt var det!</Typography>
            <Button
              size="small"
              onClick={handleReset}
              className={classes.topSpace}
            >
              Tillbaka
            </Button>
          </Grid>
        )}
      </Grid>
    </Grid>
  );
};

export default HowToStepper;
